import swiperTabHead from "../../components/index/swiper-tab-head.vue";
import daijiedan from "../../components/index/daijiedan.vue";
import baojiazhong from "../../components/index/baojiazhong.vue";
export default {
	components: {
		swiperTabHead,
		daijiedan,
		baojiazhong,
	},
	data() {
		return {
			swiperheight: 500,
			tabIndex: 0,
			iskaiqi: false,
			isAuthentication: '',
			role: '',
			tabBars: [{
					name: "待报价订单",
					id: "daibaojia"
				},
				{
					name: "已报价订单",
					id: "yibaojia"
				},
				{
					name: "接单成功订单",
					id: "daisong"
				}
			],
			newslist: [{
				list: []
			}, {
				list: []
			}, {
				list: []
			}],

		}
	},
	onLoad() { //页面加载
		uni.getSystemInfo({ //高度适配
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100)
				this.swiperheight = height;
			}
		});
		this.__init();
		//监听待接单
		uni.$on('getjinxin', (data) => {
			let list = [];
			list[0] = data;
			for (let a = 0; a < this.newslist[0].list.length; a++) {
				list[a + 1] = this.newslist[0].list[a];
			}
			this.newslist[0].list = list;
		})
		//监听关单
		uni.$on('liushui', (data) => {
			// let list = [];
			// list[0] = data;
			// for (let a = 0; a < this.newslist[0].list.length; a++) {
			// 	list[a + 1] = this.newslist[0].list[a];
			// }
			// this.newslist[0].list = list;
		})


	},
	//页面每次出现都检查是否开启接到哪如果开启刷新订单
	onShow() {
		if (this.iskaiqi) {
			this.getjinxinL();
		}
	},

	// 监听搜索框点击事件
	onNavigationBarSearchInputClicked() {

	},
	// 监听原生标题导航按钮点击事件
	onNavigationBarButtonTap(e) {

	},
	methods: {
		__init() {
			this.isAuthentication = uni.getStorageSync("dian_isAuthentication");
			this.role = uni.getStorageSync("dian_role");
			if (this.isAuthentication === 2 && this.role === 4) {
				this.getbaojiaL();
			}
		},

		jiedan() {

			if (this.isAuthentication != 2) {
				uni.showToast({
					title: "只有实名用户才能接单",
					icon: "none"
				})
			}

			if (this.role != 4) {
				uni.showToast({
					title: "只有蔬菜/百货零售商才能接单",
					icon: "none"
				})
			}
			if (!this.iskaiqi) {
				this.$chat.Open();
				//再去调用接口
				this.getjinxinL()
			} else {
				this.$chat.Close();
			}
			this.iskaiqi = !this.iskaiqi;
		},

		// tabbar点击事件
		tabtap(index) {
			this.tabIndex = index;
		},
		// 滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		//获取待报价订单
		getjinxinL() {
			let uuidform = {
				uuid: this.$http.getUuid(),
				releaseType: 4,
				orderStatus: 11
			};
			//查询有没有近3天订单
			this.$http.get(this.$urlconfig.getdaibaojia, uuidform, {}).then(data => {
				if (data.status === 0) {
					if (data.data !== null) {
						this.newslist[0].list = data.data;
					}
				}
			});
		},
		//获取报价已经报价的订单
		getbaojiaL() {
			let uuidform = {
				uuid: this.$http.getUuid(),
				releaseType: 4,
				orderStatus: 12
			};
			//查询有没有近3天订单
			this.$http.get(this.$urlconfig.getdaibaojia, uuidform, {}).then(data => {
				if (data.status === 0) {
					if (data.data !== null) {
						this.newslist[1].list = data.data;
					}
				}
			});
		}

	},


}
